.field-panel{
    position: absolute;
    right: 0;
    left:0;
    bottom: -10px;
    text-align: center;
    margin: 10px 0;
}
.row-panel ,.field-panel {
    a{
        border: 1px solid #ccc;
        padding: 10px 14px;
        text-decoration: none;
        color: #111;
        display: inline-block;
        margin-bottom: 5px;
        border-radius: 0;
        margin-left: -5px;
        background: #ffffff;
    }
    a:hover{
        background: #cccccc;
        color: #ffffff;
    }
}
.neoforms_btn-add_row{
    border: 1px solid #ccc;
    padding:10px 14px;
    text-decoration: none;
    color: #111;
    border-radius: 3px;
    display: inline-block;
    margin-bottom: 5px;
}
.neoforms_btn-add_tab{
    border: 1px solid #ccc;
    padding: 5px 14px;
    text-decoration: none;
    color: #111;
    border-radius: 3px;
    display: inline-block;
    margin-bottom: 5px;
}
.neoforms_center{
    text-align: center;
}
.neoforms_btn-flat{
    border: 1px solid #ccc;
    padding: 5px 14px;
    text-decoration: none;
    color: #111;
    border-radius: 3px;
    display: inline-block;
}
.neoforms_left{
    text-align: right;
}
.neoforms_right{
    text-align: right;
}
.neoforms_dib{
    display: inline-block;
}
.neoforms_db{
    display: block;
}

.field-panel-list{
    position: absolute;
    z-index: 9999999;
    padding: 5px;
    right: 0;
    left: 0;
    a{
        position: absolute;
        right:5px;
    }
    ul{
        margin:0;
        padding:0;
    }
    li {
        list-style: none;
        float: left;
        /*border: 1px solid #ddd;*/
        margin-bottom:0;
        margin: 0;
        cursor: pointer;
        text-align: center;
        /*padding: 5px 10px;*/
        /*border-radius: 3px;*/
        button{
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            border-radius: 0;
            background: #015395;
            color: #FFFFFF;
        }
        .field-panel-list-close:hover{
            background: #d71a1a;
            color: #FFFFFF;
        }
        &:hover{
            /*background: #cccccc;
            color: #ffffff;*/
        }
    }
}

.el-button{
    text-decoration: none;
}
/*** Edit ***/
.neoforms_form_builder_panel{
    padding-bottom: 500px;
}
.neoforms-edit-mode{
    position: relative;
    z-index:0;
    .focused_row{
        border: 1px solid #CCCCCC;
        margin-bottom: 10px;
    }
    .el-row{

    }
    .focused_row {
        .el-row{
            padding: 5px;
            min-height:50px;
            margin-top: 5px;
            margin-bottom: 5px;
            padding-bottom: 50px;
        }
        .input-col{
            position: relative;
            z-index:999999;
            a{
                text-decoration: none;
                padding: 5px 11px;
                background: #333;
                color: #fff;
            }
        }
    }
}

.neoforms_input_panel{
    position: absolute;
    text-align: center;
    right: 0;
    left: 0;
    bottom: -15px;
    a{
        /*padding: 5px 15px;*/
        display: inline;
        border-radius: 0;
        margin-left:-3px;
    }
    .neoforms_grid_panel{
        position: absolute;
        text-align: center;
        right: 0;
        left: 0;
        bottom: -20px;
        background: #ffffff;
        padding: 5px;
        border: 1px solid #ddd;
        display: inline;
        text-align: center;
        a{
            padding: 5px 10px;
            display: inline-block;
            margin: 2px;
            border: 1px solid #dddddd;
        }
    }
}
/**custom**/
.neo_entry_single_view {
    table {
        border-collapse: collapse;
        border-spacing: 0;
        width: 100% !important;
        border: 1px solid #ddd !important;
    }
    th, td {
        text-align: left;
        padding: 16px;
    }
    tr:nth-child(even) {
        background-color: #f2f2f2
    }
}

.neo_form_type {
    h4{
        margin-bottom: 10px;
    }
    h5{
        margin-top: 0px;
        margin-bottom: 20px;
    }
}

.neoforms-field-settings-panel{
    text-align: left;
    position: absolute;
    right: 0;
    left: 0;
    padding: 10px;
    overflow: hidden;
    margin-top: 5px;
    .el-form-item{
        margin-bottom: 0;
    }
    .neoforms-field-settings-action{
        text-align: right;
        display: block;
    }
    .el-form-item__content{
        padding-right: 5px;
    }
}


.neoforms_tab-panel{
    width: 500px;
    position: absolute;
    z-index: 999999;
    left: 33%;
}
.cc-edit-panel{
    max-height: 400px;
    overflow-y: scroll;
    .el-col-24{
        margin-bottom:20px;
        padding: 0 10px;
    }
    .el-input__inner{
        margin-top: 10px;
    }
    .el-row{
        padding: 0;
        min-height: auto;
        margin-top: 0;
        margin-bottom: 0;
    }
}


.panel-repeatable{
    border:1px solid #dddddd;
    padding:5px;
}
.neoforms-menu {
    a{
        text-decoration: none;
    }
    .is-active{
        background: #409EFF !important;
        color: #ffffff !important;
    }
}
.pro_fields{
    border: 1px solid #cc0000;
    padding: 5px;
    color: #cc0000;
    padding: 8px 10px !important;
    border-radius: 5px;
    p.description{
        color: #cc0000;
    }
}

/*common*/
.neoforms_layout_cornered input[type="text"],
.neoforms_layout_cornered input[type="email"],
.neoforms_layout_cornered input[type="url"],
.neoforms_layout_cornered input[type="password"],
.neoforms_layout_cornered input[type="search"],
.neoforms_layout_cornered input[type="number"],
.neoforms_layout_cornered input[type="tel"],
.neoforms_layout_cornered input[type="range"],
.neoforms_layout_cornered input[type="date"],
.neoforms_layout_cornered input[type="month"],
.neoforms_layout_cornered input[type="week"],
.neoforms_layout_cornered input[type="time"],
.neoforms_layout_cornered input[type="datetime"],
.neoforms_layout_cornered input[type="datetime-local"],
.neoforms_layout_cornered input[type="color"],
.neoforms_layout_cornered textarea{
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
}
.active_col_btn{
    background: #fff !important;
    color: #000 !important;
    border: 2px solid #000 !important;
}
.neo_tabs{
    .tabs{
        background: #015395;
        a{
            padding: 15px 10px;
            display: block;
            /*float: left;*/
          text-align: center;
          font-weight: bold;
          font-size: 15px;
            text-decoration: none;
            color: #fff;
            &:hover{
                background: #ecf5ff;
                color: #3a8ee6;
            }
        }
    }
    .tab_cotnent{
        background: #FFFFFF;
    }
}
.neo_element_tree{
    .neo_tree_tab{
        .neo_tree_tab_span{
            display: block;
            background: #555;
            padding: 10px 10px;
            color: #fff;
            a{
                text-decoration: none;
                color: #FFFFFF;
                border:1px solid #FFFFFF;
                padding:0px 5px 2px 5px;
                right: 10px;
            }
        }
        .neo_tree_placeholder{
            margin-left: 10px;
            .neo_tree_placeholder_span{

            }
        }
    }
    .neo_tree_placeholder{
        .neo_tree_placeholder_span{
            display: block;
            background: #555;
            padding: 10px 10px;
            color: #fff;
            margin-top: 3px;
            a{
                text-decoration: none;
                color: #FFFFFF;
                border:1px solid #FFFFFF;
                padding:0px 5px 2px 5px;
                right: 10px;
            }
        }
    }

    li.neo_tree_field{
        margin-left: 10px;
        .neo_tree_field_span{
            border: 1px solid #015395;
            display: block;
            padding: 5px 10px;
            margin-top: 3px;
            a{
                text-decoration: none;
                border:1px solid #015395;
                padding:0px 5px 2px 5px;
                right: 10px;
            }
        }
        .neo_tree_field_settings{
            border:1px solid #ccc;
            padding:0 10px;
        }
    }
}
.action_panel{
    padding: 10px;
    background: #fff;
}
